@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: 'Montserrat', sans-serif;
}
input[type='text'] , input[type='number'] , input[type='password'] , input[type='email']{
    outline : none;
    margin-left : auto;
    margin-right : auto;
    margin-top: 1em;
    margin-bottom : 1em;
    display: block;
    width : 80%;
    padding-left:1em;
    height: 2.5em;
    font-size: 0.9em;
    font-family: 'Montserrat', sans-serif;
    box-shadow: 5px 3px 25px -9px rgba(47,75,38,1);

    border : none;
}

.btn {
    background-color: #CBFF4D;
    color : #545454;
    text-decoration: none;
    padding : 0.7em;
    margin : 0.4em;

}


   
  .fullscreen-enabled {
    background: #fff;
  }
.heading {
    text-align: center;
    color : #2F4B26;
    box-shadow: 4px 3px 14px -4px rgba(47,75,38,1);
    padding : 0.7em;
    margin-top : 0;


}

.forms{
    display : grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1em;
}

.login-form, .signup-form{
    padding: 1em;
    box-shadow: 4px 3px 14px -4px rgba(47,75,38,1);
}


.sub-heading{
    text-align: center;
    color: #23CE6B;
}



label{
    font-size : 0.8em;
    
}

.login-button{
    display : block;
    margin : auto;
    width : 50%;
    height : 4em;
    font-family: 'Montserrat', sans-serif;
    background-color: #23CE6B;
    border: none;
    outline : none;
    color: aliceblue;
}
.login-button:hover{
    cursor: pointer;
}
.signup-button{
    display : block;
    margin : auto;
    width : 50%;
    height : 4em;
    font-family: 'Montserrat', sans-serif;
    background-color: #A846A0;
    border: none;
    outline : none;
    color: aliceblue;
}
.signup-button:hover{
    cursor: pointer;
}

.profile{
    display : grid;
    grid-template-columns: 2fr 3fr 3fr;
    grid-gap: 1em;
    padding-right : 1em;
    color: #545454;
}

.profile-section {
     background-color: #CBFF4D;
     height: 100vh;
    box-shadow: 10px 11px 14px -5px rgba(107,170,118,1);
    text-align: center;
    display: block;
    overflow: auto;
}

.exam-section{
    

    padding : 0 1em;
    box-shadow: 10px 11px 14px -5px rgba(107,170,118,1);
    height: 100vh;
    display: block;
    overflow: auto;
}

.report{
    box-shadow: 10px 11px 14px -5px rgba(107,170,118,1);
    height: 100vh;
    display: block;
    overflow: auto;
}

.username{

    margin : 0.3em;
    background-color: #545454;
    color: #CBFF4D;
    padding : 0.5em;
}

.signout-btn:hover {
    cursor: pointer;
}

.quiz-card{
    text-align: center;
    box-shadow: 10px 10px 31px -15px rgba(0,0,0,0.17), -8px -7px 31px -15px rgba(0,0,0,0.17);
    overflow: auto;
    padding : 0.75em 0.7em;
    border-radius: 0.5em;
    margin-bottom: 2em;
}
.quiz-card:last-child{
    margin-bottom: 0;
}

.quiz-card h4{
    text-transform: capitalize;
    font-size : 1.3em;
    color : #84DD63;
}

::-webkit-scrollbar {
    width: 5px;
    height: 8px;
    background-color: #aaa; /* or add it to the track */
    display:block;
  }
  .profile-image {
      width : 100%;
  }
  .report{
      padding : 1em;
  }

.quiz-details{
    text-align: center;
}

.quiz-details img{
    width : 100%;
    height: 20em;
    object-fit: cover;
    
    -webkit-filter: brightness(-5);
    
            filter: brightness(-5)
}

.exam{
    display:grid;
    grid-template-columns: 2fr 6fr;
    grid-template-rows: 1fr 5fr;
    height : 100vh;
    grid-gap: 1em;
}
.information{

    grid-column-start: 1;
    grid-column-end:  3;

    grid-row-start: 1;
    grid-row-end: 2;
    box-shadow: 6px 6px 17px -5px rgba(84,84,84,1);
    display : grid;
    grid-template-columns: 3fr 2fr 3fr 1fr;
    grid-gap : 0;
    
}



.question{

    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
    box-shadow: 6px 6px 17px -5px rgba(84,84,84,1);

    padding : 1em;
}

.Question-ul{
    list-style-type: none;
    padding : 0;

}
.Question-ul li{
    
    margin:0;
    padding : 1em;
    border-bottom : 0.1em solid black;
    
}

.Question-ul li:hover{
    cursor: pointer;
    color : #F3FCF0;
    background-color : #1F271B;
    border-bottom : 0;
}

.question-index{
    border-right : 1px solid #9C3848;
    padding-right: 0.5em;
}

input[type='checkbox']{
    display: inline-block;
    padding : 0;
}

.marks{

    background-color: #A5BE00;
    color : #EBF2FA;
    display: inline-block;
    padding: 1em;
    border-radius: 2em;
}

.quiz-info{
    padding : 1em;
    background-color: #9BC53D;
}

.option{
    box-shadow: 6px 6px 17px -5px rgba(84,84,84,1);
    padding : 0.5em;
    
}

.option > input[type='checkbox']:hover{
    cursor: pointer;
}
